import Trash from './components/Trash'
import Spam from './components/Spam'
import Inbox from './components/Inbox'
import React from "react";
import {render} from "react-dom"
import FaTrash from 'react-icons/lib/fa/trash';
import FaInbox from 'react-icons/lib/fa/inbox';
import FaBug from 'react-icons/lib/fa/bug';
import {
  BrowserRouter as Router,
  Route,
} from 'react-router-dom'

import createBrowserHistory from 'history/createBrowserHistory';
import { NavLink } from 'react-router-dom'
import registerServiceWorker from './registerServiceWorker';


const history = createBrowserHistory(

  )



render(

<Router>
   <div className="navHeader">
          <div className="mailListHeader">
                
                <span activClassName="Inbox-link"><NavLink to='/Inbox' activeStyle={{ color: '#4990E2' , border: 'none' }} onClickInbox={this.OnClickInbox}><FaInbox className= "Icon"/>Inbox</NavLink></span>
                <span activeClassName="Trash-link"><NavLink to='/Trash' activeStyle={{ color: '#4990E2', border: 'none' }} onClicTrash={this.OnClickTrash}><FaTrash className= "Icon" />Trash</NavLink></span>
                <span activeClassName="Spam-link"><NavLink to='/Spam' activeStyle={{ color: '#4990E2', border: 'none' }} onClickSpam={this.OnClickSpam}><FaBug className= "Icon" />Spam</NavLink></span>
              </div>     
       <Route exact path="/" component={Inbox}/>
      <Route exact path="/Inbox" component={Inbox} history={history}/>
      <Route exact path="/Trash" component={Trash} history={history}/>
      <Route exact path="/Spam" component={Spam} history={history}/>
     
  
    </div>
  </Router>,
 window.document.getElementById('root')
);registerServiceWorker();



